import anime from 'animejs';
import classnames from 'classnames';
import throttle from 'lodash.throttle';
import { useEffect, useState } from 'react';
import styles from './BackgroundScene.module.scss';

const BackgroundScene = () => {
  const [initialOffset, setInitialOffset] = useState<number>(0);
  const handleScroll = () => {
    const newOffset = initialOffset - window.pageYOffset;
    const easing = 'easeOutSine';
    anime({
      targets: '[data-anime=blobs--fast]',
      translateY: newOffset * 0.3,
      easing,
    });
    anime({
      targets: '[data-anime=blobs--slow]',
      translateY: newOffset * 0.1,
      easing,
    });
    anime({
      targets: '[data-anime=blobs--normal]',
      translateY: newOffset * 0.05,
      easing,
    });
  };

  useEffect(() => {
    setInitialOffset(window.pageYOffset);
    if (window.matchMedia(`(min-width: 769px)`).matches) {
      window.addEventListener('scroll', throttle(handleScroll, 100));
    }
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);
  return (
    <>
      <div className={classnames(styles['blur'], styles['blur--level-0'])}></div>
      <div
        className={classnames(styles['blobs'], styles['blobs--fast'])}
        data-anime='blobs--fast'></div>
      <div className={classnames(styles['blur'], styles['blur--level-1'])}></div>
      <div
        className={classnames(styles['blobs'], styles['blobs--normal'])}
        data-anime='blobs--normal'></div>
      <div className={classnames(styles['blur'], styles['blur--level-2'])}></div>
      <div
        className={classnames(styles['blobs'], styles['blobs--slow'])}
        data-anime='blobs--slow'></div>
    </>
  );
};

export default BackgroundScene;
