import classNames from 'classnames';
import React, { ReactNode } from 'react';
import Tags from '../Tags';
import Title from '../Title';
import styles from './Card.module.scss';

interface CardProps {
  children: ReactNode;
  icon?: JSX.Element;
  title: string;
  tags?: string[];
  decorated?: boolean;
}

const Card = ({ children, icon, title, tags, decorated }: CardProps) => {
  return (
    <div className={classNames(styles['Card'], decorated && styles['Card--decorated'])}>
      <Title icon={icon} title={title}></Title>
      <div className={styles['Card__content']}>{children}</div>
      {tags && <Tags tags={tags} />}
    </div>
  );
};

export default Card;
