import uniqid from 'uniqid';
import styles from './Tags.module.scss';

interface TagsProps {
  tags: string[];
}

const Tags = ({ tags }: TagsProps) => {
  return (
    <div className={styles['Tags']}>
      {tags.map((tag) => (
        <div key={uniqid()} className={styles['Tags__item']}>
          {tag}
        </div>
      ))}
    </div>
  );
};

export default Tags;
